import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import AddSupportRequestModal from './AddSupportRequestModal';
import { Table } from '../../../components';
import { axios } from '../../../http';
import SetTokenHeader from '../../../hoc/SetTokenHeader/SetTokenHeader';
import { getAllSupportRequestSaga } from '../../../store/actions';
import AlertMessageModal from '../../../components/UI/Model/AlertMessageModal';

const tableFields = [
  {
    displayName: 'Date & Time',
    key: 'createdAt',
    isDateTime: true,
  },
  {
    displayName: 'Message',
    key: 'message',
  },
  {
    displayName: 'Status',
    key: 'isResolved',
    noSort: true,
    isConditional: true,
    positiveValue: 'Resolved',
    negativeValue: 'Not Resolved',
  },
];

// eslint-disable-next-line no-unused-vars
const SupportRequest = props => {
  const [modelOpen, setModelOpen] = useState(false);
  const [mounted, setMounted] = useState(false);
  // eslint-disable-next-line no-unused-vars
  const [searchFlag, setSearchFlag] = useState(false);
  const [tableFilter, setTableFilter] = useState({});
  const setTableFilters = (columnName, orderBy, search, skip, limit) => {
    tableFilter.columnName = columnName;
    tableFilter.orderBy = orderBy;
    tableFilter.search = search;
    tableFilter.skip = skip;
    tableFilter.limit = limit;
    setTableFilter(tableFilter);
  };
  const {
    supportRequests: { list: allData, totalRecords },
    // eslint-disable-next-line no-unused-vars
    errorMsg,
  } = useSelector(state => state.supportRequest);
  const { open } = useSelector(state => state.modal);

  const dispatch = useDispatch();

  const getData = (columnName, orderBy, search, skip, limit) => {
    setTableFilters(columnName, orderBy, search, skip, limit);
    if (search && skip !== 0) {
      dispatch(
        getAllSupportRequestSaga({
          columnName,
          orderBy,
          search,
          skip: 0,
          limit,
        }),
      );
    } else {
      dispatch(
        getAllSupportRequestSaga({
          columnName,
          orderBy,
          search,
          skip,
          limit,
        }),
      );
    }
  };

  useEffect(() => {
    setMounted(true);
    getData('createdAt', 'desc', '', 0, 10);
    return () => setMounted(false);
  }, []);

  // eslint-disable-next-line no-unused-vars
  const statusAction = data => {};

  const linkObj = {};

  const searchFlagSetter = val => {
    if (mounted) {
      setSearchFlag(val);
    }
  };

  return (
    <div className="wrapper wrapper-content">
      <div className="row">
        <div className="col-lg-12 col-md-12 col-sm-12 col-xs-12">
          <div className="ibox">
            <div className="ibox-content">
              <div className="row m-b-sm">
                <div className="col-md-6">
                  <div className="box-heading">
                    <h2>Support Request</h2>
                    <p style={{ fontWeight: 'bold' }}>
                      For any emergency you can call us on&nbsp;
                      <a href="tel:XXXX-XXX-XXXX">XXXX-XXX-XXXX</a>
                    </p>
                  </div>
                </div>
                <div className="col-md-6 text-right">
                  <button
                    type="button"
                    className="btn btn-primary"
                    data-toggle="modal"
                    onClick={() => setModelOpen(true)}
                  >
                    Add Request
                  </button>
                </div>
              </div>
              <div className="coman-table" id="owner-detail-btn">
                <Table
                  headers={tableFields}
                  tableData={allData || null}
                  pageCount={totalRecords?.count}
                  getData={getData}
                  link={linkObj}
                  linkPropsName="_id"
                  statusAction={statusAction}
                  setSearchFlag={searchFlagSetter}
                  errorMsg={errorMsg || 'Loading...'}
                  initialSortColumnName="createdAt"
                  initialSortOrderBy="desc"
                />
              </div>
              <p style={{ fontWeight: 'bold' }}>
                For more information you can email us on&nbsp;
                <a href="mailto:query@example.com">query@example.com</a>
              </p>
            </div>
          </div>
        </div>
      </div>
      {modelOpen && (
        <AddSupportRequestModal
          setModelOpen={setModelOpen}
          tableFilter={tableFilter}
        />
      )}
      {open && <AlertMessageModal />}
    </div>
  );
};

export default SetTokenHeader(SupportRequest, axios);
