import React, { useState } from 'react';
import * as Yup from 'yup';
import { Formik } from 'formik';
import { useDispatch, useSelector } from 'react-redux';
import Model from '../../../components/UI/Model/Model';
import { addSupportRequestSaga, resetErrorMsg } from '../../../store/actions';
import { ENTER_MESSAGE } from '../../../constants/errorConstants';

// eslint-disable-next-line no-unused-vars
const AddSupportRequestModal = props => {
  const [isDisable, setIsDisable] = useState(false);
  const { tableFilter } = props;

  const dispatch = useDispatch();

  const {
    // eslint-disable-next-line no-unused-vars
    errorMsgAddSupportRequest,
  } = useSelector(state => state.supportRequest);

  const closeModel = () => {
    const { setModelOpen } = props;
    setModelOpen(false);
    dispatch(resetErrorMsg());
  };

  const validationSchema = Yup.object({
    message: Yup.string().trim().required(ENTER_MESSAGE),
  });

  const submitBtnHandler = values => {
    setIsDisable(true);
    dispatch(
      addSupportRequestSaga({
        ...values,
        tableFilter,
        closeModel,
        setIsDisable,
      }),
    );
  };

  return (
    <Model
      closeModel={closeModel}
      isDisable={isDisable}
      headerTitle="Generate the support ticket"
    >
      <h3 className="text-left required-field">Message:</h3>
      <Formik
        initialValues={{
          message: '',
        }}
        validationSchema={validationSchema}
        onSubmit={(values, { setSubmitting }) => {
          submitBtnHandler(values);
          setSubmitting(false);
        }}
      >
        {({
          values,
          errors,
          touched,
          handleChange,
          handleBlur,
          handleSubmit,
          // isSubmitting,
        }) => (
          <form onSubmit={handleSubmit}>
            <textarea
              className="form-control"
              style={{ height: '200px !important' }}
              name="message"
              value={values.message}
              onChange={handleChange}
              onBlur={handleBlur}
            />
            <div className="error-message">
              {errors.message && touched.message && errors.message}
            </div>
            <div className="error-message">{errorMsgAddSupportRequest}</div>
            <div className="clearfix">
              <button
                type="submit"
                disabled={isDisable}
                style={{ width: '100px', borderRadius: '0' }}
                className="btn btn-primary m-r-sm"
              >
                Submit
              </button>
              <button
                type="button"
                disabled={isDisable}
                style={{ width: '100px' }}
                className="btn btn-danger m-r-sm"
                data-dismiss="modal"
                onClick={closeModel}
              >
                Cancel
              </button>
            </div>
          </form>
        )}
      </Formik>
    </Model>
  );
};

export default AddSupportRequestModal;
